@keyframes float-slow {
	0%,
	100% {
		transform: translateY(0px) scale(1);
	}
	50% {
		transform: translateY(-10px) scale(1.05);
	}
}
@keyframes float-delayed {
	0%,
	100% {
		transform: translateY(0px) scale(1);
	}
	50% {
		transform: translateY(-8px) scale(1.03);
	}
}
@keyframes pulse-slow {
	0%,
	100% {
		opacity: 0.1;
		transform: scale(1);
	}
	50% {
		opacity: 0.15;
		transform: scale(1.1);
	}
}
@keyframes twinkle-1 {
	0%,
	100% {
		opacity: 0.3;
		transform: scale(1);
	}
	50% {
		opacity: 1;
		transform: scale(1.3);
	}
}
@keyframes twinkle-2 {
	0%,
	100% {
		opacity: 0.4;
		transform: scale(1);
	}
	50% {
		opacity: 0.8;
		transform: scale(1.2);
	}
}
@keyframes twinkle-3 {
	0%,
	100% {
		opacity: 0.5;
		transform: scale(1);
	}
	50% {
		opacity: 0.9;
		transform: scale(1.2);
	}
}
@keyframes twinkle-4 {
	0%,
	100% {
		opacity: 0.3;
		transform: scale(1);
	}
	50% {
		opacity: 0.7;
		transform: scale(1.1);
	}
}
@keyframes shimmer-slow {
	0% {
		transform: translateX(-100%);
	}
	100% {
		transform: translateX(100%);
	}
}
.animate-float-slow {
	animation: float-slow 8s ease-in-out infinite;
}
.animate-float-delayed {
	animation: float-delayed 10s ease-in-out infinite 2s;
}
.animate-pulse-slow {
	animation: pulse-slow 6s ease-in-out infinite;
}
.animate-twinkle-1 {
	animation: twinkle-1 3s ease-in-out infinite;
}
.animate-twinkle-2 {
	animation: twinkle-2 4s ease-in-out infinite 1s;
}
.animate-twinkle-3 {
	animation: twinkle-3 5s ease-in-out infinite 2s;
}
.animate-twinkle-4 {
	animation: twinkle-4 3.5s ease-in-out infinite 1.5s;
}
.animate-shimmer-slow {
	animation: shimmer-slow 8s ease-in-out infinite;
}
